.ant-image-mask {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    background: transparent !important;
    cursor: pointer;
    opacity: 0;
    transition: opacity 0.3s;
}